export function RoleIguacuano() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
      <Button onClick={handleOpen} variant="outlined" >Leia mais</Button>

      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={open}
        onClose={handleClose}
        closeAfterTransition
        disableEnforceFocus
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <Box sx={style}>
            <IconButton onClick={handleClose} className="closebutton">
              <Close />
            </IconButton>
            <CardMedia
              style={{ marginTop: '2%', maxWidth: 400 }}
              component="img"
              height="400"
              image='https://www.alessandromusetta.com/geo/tiles/tingua/data/20221227/316053953_701024761152361_1630399678539113923_n.jpg'
              alt=""
            />
            <Typography id="modal-modal-description" sx={{ mt: 2 }}>
              Governo do Estado do Rio de Janeiro, Secretaria de Cultura de Estado de Cultura e Economia Criativa do Rio de Janeiro atravs do Edital Cultura Presente nas Redes 2 apresentam Rol Iguauano.
            </Typography>
            <Typography id="modal-modal-description" sx={{ mt: 2 }}>
              No Rol Iguauano de hoje, indicamos a vocs o EcoMuseu Iguass que fica em Tingu na Rua Paran, 34. <br /><br />
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', maxWidth: 400 }}>
              <ReactPlayer height='100%' controls url="https://www.alessandromusetta.com/geo/tiles/tingua/data/20221227/Role-Iguacuano.mp4" />
            </Box>
          </Box>
        </Fade>
      </Modal>
    </div>
  );
}

